
.result-text-first{
    color: gray;
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: .2px;
    font-weight: 400;
    margin-left: 20px;
    margin-top: 20px;
}

strong{
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    color: #86e0d1

}

.extra-info{
    background-color: none;
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    width: 380px;
    border: 3px solid #86e0d1;
    padding: 15px;
    border-radius: 5px;
    -ms-flex-line-pack: center;
        align-content: center;
    margin: auto;
    margin-top: 20px;
    font-style: italic; 
    font-weight: 300;
    font-size: 18px;
    color: #6e64c5
}

.title{
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 30px;
    text-align: center;
    line-height: 30px;
    font-weight: 300;
    text-transform: uppercase;
    color: #6e64c5;
    letter-spacing: 1px;
    height: 12px;
    font-style: italic;
    margin-top: 0;
    padding-bottom: 20px;
    padding-top: 0px;

}

.stage {
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 20px;
    text-align: center;
    line-height: 30px;
    font-weight: 300;
    text-transform: uppercase;
    color: #6e64c5;
    letter-spacing: 1px;
    height: 12px;
    font-style: italic;
    margin-top: 30px;
    padding-bottom: 20px;
    padding-top: 0px;
}	


