.navbar{
    padding: 0px;
    margin: 0px;
    height: auto;
}

.nav-logo{
    float: right;
    margin-top: 8px;
    margin-right: 30px
}

ol, ul {
    list-style: none;
}
li {
    display: inline-block;
    padding: 20px 0 10px;
    vertical-align: middle;
}
a:hover, a:focus, a:active {
    color: #4A4FEF;
    text-decoration: none;
}
a {
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    font-weight: lighter;
    text-decoration: none;
    -webkit-transition: color 0.1s, background-color 0.1s;
    -o-transition: color 0.1s, background-color 0.1s;
    transition: color 0.1s, background-color 0.1s;
}
a {
    position: relative;
    display: block;
    padding: 16px 0;
    margin: 0 12px;
    letter-spacing: 1px;
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    text-transform: uppercase;
    -webkit-transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    -o-transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    color: #4A4FEF;
}
a::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 3px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: #4A4FEF;
    -webkit-transform-origin: right top;
        -ms-transform-origin: right top;
            transform-origin: right top;
    -webkit-transform: scale(0, 1);
        -ms-transform: scale(0, 1);
            transform: scale(0, 1);
    -webkit-transition: color 0.1s,-webkit-transform 0.2s ease-out;
    transition: color 0.1s,-webkit-transform 0.2s ease-out;
    -o-transition: color 0.1s,transform 0.2s ease-out;
    transition: color 0.1s,transform 0.2s ease-out;
    transition: color 0.1s,transform 0.2s ease-out,-webkit-transform 0.2s ease-out;
}
a:active::before {
    background-color: #4A4FEF;
}
a:hover::before, a:focus::before {
    -webkit-transform-origin: left top;
        -ms-transform-origin: left top;
            transform-origin: left top;
    -webkit-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
            transform: scale(1, 1);
    background-color: #82E1D1;

}



