h1.toggle-quote{
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    text-transform: uppercase;
    font-size: 5vw;
    color: white;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    width: 100%;
    text-align: center;
    height: 170px;
    left: 50%;
    z-index: 1
}


/* Smartphones (landscape) ----------- */
@media (min-width : 0px) and (max-width : 320px) {
/* Styles */
    h1.toggle-quote{
        top: 135px ;
        height: 50px;
    }
}

@media (min-width : 321px) and (max-width : 475px) {
/* Styles */
    h1.toggle-quote{
        top: 165px ;
        height: 80px;
    }
}


@media (min-width : 476px) and (max-width : 550px) {
/* Styles */
    h1.toggle-quote{
        top: 210px ;
        height: 140px;
    }
}

@media (min-width : 551px) and (max-width : 767px) {
/* Styles */
    h1.toggle-quote{
        top: 220px ;
        height: 150px;
    }
}


/* Smartphones (landscape) ----------- */
@media (min-width : 768px) and (max-width : 899px) {
/* Styles */
    h1.toggle-quote{
        top: 260px ;
    }
}

/* Smartphones (landscape) ----------- */
@media (min-width : 900px) and (max-width : 1024px) {
/* Styles */
    h1.toggle-quote{
        top: 280px ;
    }
}

/* Smartphones (landscape) ----------- */
@media (min-width : 1025px) and (max-width : 1223px) {
/* Styles */
    h1.toggle-quote{
        top: 300px ;
    }
}


/* Smartphones (landscape) ----------- */
@media (min-width :  1224px) {
/* Styles */
    h1.toggle-quote{
        top: 335px ;
    }
}




